import React from "react";
import s from './RecommendsGallery.module.css'
import {setPhotosAPI} from "../../../api/api";

export const RecommendsGallery = (props) => {
    return (
        <div className={s.RecommendsGallery}>
            {
                props.keyWords.map(word => {
                    setPhotosAPI(1, true, word).then(res => {
                        let a = typeof res.photos
                        debugger
                        return (
                            <div>
                                {word}
                                <img src={res.photos[0].src.large} alt=""/>
                            </div>
                        )
                    })
                })
            }
        </div>
    )
}